/* ------------------------------------------------------------------------------------------------ */
/* Animation */
/* ------------------------------------------------------------------------------------------------ */


.bounce-1 {
  animation-name: bounce-1;
  animation-timing-function: linear; }
@keyframes bounce-1 {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-10px); }
  100% {
    ransform: translateY(0); } }
